import styles from './styles.module.css';
import {Answer} from "./answer";
import {useOnboardingContext} from '../../../../context/onboarding-context';
import {useMemo} from "react";

export const Answers = () => {
  const {step, data, result, setResult} = useOnboardingContext();
  const renderData = data.pages[step];

  const checkType = useMemo(() => {
    return renderData.type === 'gender_picker' || (renderData.resources.survey_max_answers_count === 1) ? 'radio' : 'checkbox'
  }, [renderData]);

  const onCheck = (key: string, uncheck: boolean) => {
    if (result[renderData.id] && Number(renderData.resources.survey_max_answers_count) <= result[renderData.id].length) {
      return;
    }

    if (uncheck) {
      const arr = [...result[renderData.id]];
      const index = arr.indexOf(key);

      arr.splice(index, 1);
      setResult({...result, [renderData.id]: arr});

      return;
    }

    setResult({...result, [renderData.id]: result[renderData.id] && checkType !== 'radio' ? [...result[renderData.id], key] : [key]});
  }

  return (
    <div className={styles.answers}>
      {renderData.resources.survey_options.map((item, index) => (
        <Answer
          key={item.title.key}
          inputName={renderData.id}
          type={checkType}
          name={item.title.key}
          label={item.title.display}
          index={index}
          onCheck={onCheck}
        />
      ))}
    </div>
  )
}
