import styles from './button.module.css';
import {useOnboardingContext} from '../../../../context/onboarding-context';

export const Button = () => {
  const {step, setStep, data, result} = useOnboardingContext();
  const stepData = data.pages[step];

  const onClick = () => {
    if ((data.pages.length - 1) > step) {
      setStep(step + 1);
    } else {
      document.location.replace(document.location.pathname + '#paywall');
      document.location.reload();
    }
  }

  const isDisabled = !stepData.resources.is_skipable && !result[stepData.id]?.length;

  return (
    <div className={styles.wrapper}>
      <button
        className={styles.button}
        onClick={onClick}
        disabled={isDisabled}
      >
        Continue
      </button>
    </div>
  )
}
