import styles from './paywall.module.css';
import Logo from '../../assets/logo.svg';
import {useEffect, useState} from "react";
import CloseIcon from '../../assets/close-icon.svg';
import * as amplitude from "@amplitude/analytics-browser";

export const Paywall = () => {
  const [showCloseButton, setShowCloseButton] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setShowCloseButton(true);
    }, 3000);
    amplitude.track('show_paywall_screen');
  }, []);

  const track = (id: string) => {
    amplitude.track('paywall_product_tap', {
      id
    });
  }

  const handlerContinue = () => {
    amplitude.track('paywall_purchase_tap');
  }

  return (
    <div className={styles.videoBg}>
      <video playsInline autoPlay loop muted>
        <source src='https://dev-cdn.nufa.ai/web/paywall.mp4' type="video/mp4"/>
      </video>
      <div className={styles.videoBgEffect}></div>
      <div className={styles.container}>
        <div className={styles.content}>
          <div className={styles.logo}>
            <img src={Logo} alt='Nufa'/>
            <span className={styles.logoLabel}>PRO</span>
          </div>
          <div>
            <h3>Get full access</h3>
            <ul>
              <li>
                New Filters Every Day
              </li>
              <li>
                Instant processing
              </li>
              <li>
                Hight Resolution
              </li>
              <li>
                No Limits
              </li>
            </ul>
          </div>
          <div className={styles.plans}>
            <div className={styles.plan}>
              <input type='radio' name='plan' id='plan1' hidden onClick={() => track('prod_QuOGTfNQQXr6B8')}/>
              <label htmlFor='plan1'>
                <span className={styles.costPeriod}>Weekly $7.99</span>
                <span className={styles.costDay}>$0.90 per day</span>
              </label>
            </div>
            <div className={styles.plan}>
              <input type='radio' name='plan' id='plan2' hidden defaultChecked onClick={() => track('prod_QuOJDWVs7a86L3')}/>
              <label htmlFor='plan2'>
                <span className={styles.costPeriod}>Yearly $46.99</span>
                <span className={styles.costDay}>$0.13 per day</span>
              </label>
              <div className={styles.discount}>90% OFF</div>
            </div>
          </div>
          <button className={styles.button} onClick={handlerContinue}>Continue</button>
        </div>
      </div>
      {showCloseButton && <a href='https://nufa.onelink.me/OSCY/rvrapu8n' className={styles.closeButton}>
        <img src={CloseIcon} alt='close'/>
      </a>}
    </div>
  )
}
